import React, {Component} from "react";
import {connect} from "react-redux";
import OraModal from "eshop/modules/core/components/ui/Modal";
import {hideProcessAlertModal} from "eshop/modules/auth/actions/authorization"
import {getRetentionAlertModalVisibility} from "eshop/modules/auth/selectors/authorization"

class ProcessAlertModalComponent extends React.Component{
    constructor(props){
        super(props);
    }
    onSubmit(event){
        event.preventDefault();
     }
     onClose(){
        this.props.hideProcessAlertModal();
     }

    getModalProps() {
        return {
            size: "narrow",
            escapeClose: false,
            clickClose: false
        };
    }

    getTitle(){
        if(this.props.showRetentionAlertModal)
            return this.props.descriptions&&this.props.descriptions.retentionAlertTitle||"Komunikat";
        return "Komunikat";

    }
    getInfo(){

        if(this.props.showRetentionAlertModal)
            return this.props.descriptions&&this.props.descriptions.retentionAlertBody||("W koszyku znajduje się usługa utrzymania. Nie można zmienić konta bilingowego! ")
        return "Nie można zmienić konta bilingowego dla tego procesu!"
    }
    render(){


    return (
        <OraModal  {...this.getModalProps()} showClose={true} open={this.props.showProcessAlertModal} onClose={this.onClose.bind(this)} id="process-alert-modal">
            <div className="l-row">
                <div className="l-col l-col-12">
                    <h2>{this.getTitle()}</h2>
                </div>
            </div>
            <div className="l-row">
                <div className="l-col l-col-12">
                    {this.getInfo()}
                </div>
            </div>
            <form id="processAlertForm" onSubmit={this.onSubmit.bind(this)}>
            <div className="l-row ">
                <div className="l-col l-col-12 ">
                    <div className="o-separator o-separator--s u-spacing-top-l"></div>
                </div>
            </div>
            <div className="l-row ">
                <div className="l-col l-col-8 l-col-small-12">
                    </div>


                    <div className="l-col l-col-4 l-col-small-12 u-padding-top-l">
                        <button id="cancelLogout" type="button"  onClick={this.onClose.bind(this)} className="o-btn opl-btn opl-btn--primary u-w-100">{this.props.descriptions.processAlertCancel||"Zamknij"}</button>
                    </div>
            </div>
            </form>
        </OraModal>)
    }
}
;

const mapStateToProps = state => ({
    showRetentionAlertModal:getRetentionAlertModalVisibility(state),
    showProcessAlertModal:getRetentionAlertModalVisibility(state)
});

const mapDispatchToProps = dispatch => ({
    hideProcessAlertModal:()=>dispatch(hideProcessAlertModal())
});

const ProcessAlertModal = connect(mapStateToProps, mapDispatchToProps)(ProcessAlertModalComponent);
export default ProcessAlertModal;

